<template>
  <x-header>
    黄历
  </x-header>
  <div class="" v-touch:swipeleft="onSwipeLeft" v-touch:swiperight="onSwipeRight">
    <inline-calendar
    v-ref:calendar
    class="inline-calendar-demo"
    :value.sync="value"
    start-date="2016-04-01"
    end-date="2017-06-18"
    :highlight-weekend="highlightWeekend"
    :weeks-list="weeksList">
    </inline-calendar>
    <article class="weui_article" style="text-align:center;">
      <h1 style="font-size:40px;color:#23A05A;">{{result.lunar}} {{result.lunarYear}}[{{result.animalsYear}}]</h1>
      <section>
        <h2 style="font-size:30px;color:#919191;" class="title">宜：{{result.suit}}</h2>
        <h2 style="font-size:30px;color:#919191;" class="title">忌：{{result.avoid}}</h2>
      </section>
    </article>
  </div>
</template>
<script>
  import XHeader from 'vux/components/x-header'
  import InlineCalendar from '../components/inline-calendar'
  import { showAlert, showLoading, hideLoading } from '../vuex/actions'

  export default {
    vuex: {
      actions: {
        showAlert,
        showLoading,
        hideLoading
      }
    },
    data () {
      return {
        leftOptions: {
          showBack: true,
          backText: 'Back',
          preventGoBack: true
        },
        highlightWeekend: true,
        weeksList: ['日', '一', '二', '三', '四', '五', '六'],
        value: '',
        result: {}
      }
    },
    methods: {
      onSwipeLeft () {
        this.$refs.calendar.prev()
      },
      onSwipeRight () {
        this.$refs.calendar.next()
      }
    },
    events: {
      'select-date' (date) {
        this.$http.get('/api/calendar/day', { date: date, key: '50b1b6209ddd79e0562ce0f45d6c3c69' }).then(function (response) {
          this.$set('result', response.data.result.data)
        }, function (response) {
          this.showAlert(response.data.title, response.data.error)
        })
      }
    },
    components: {
      XHeader,
      InlineCalendar
    }
  }
</script>



